/* styles/Home.css */

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  opacity: 0;
  -webkit-animation: fadeIn 1s ease-in-out forwards;
          animation: fadeIn 1s ease-in-out forwards;
  background: linear-gradient(to bottom right, #1a2a6c, #b21f1f, #fdbb2d);
  height: 100vh;
}
@media screen and (max-width: 500px) {
  .home {
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  padding: 3rem;
  border-radius: 8px;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.1);
  background-color: white;
}

.about h2 {
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  position: relative;
}

.about h2::before {
  content: "";
  display: inline-block;
  width: 0;
  height: 100%;
  vertical-align: bottom;
  margin-right: -0.2em;
  -webkit-animation: writing 3s steps(30, end) forwards;
          animation: writing 3s steps(30, end) forwards;
}

@-webkit-keyframes writing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes writing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .about h2 {
    font-size: 1.5rem;
  }
}

.prompt {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  text-align: center;
}

.prompt p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.icons-all {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.icons-all a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  background-color: #eee;
  transition: all 0.3s ease;
}

.icons-all a:hover {
  background-color: #0077b5;
  color: white;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.icons-all a svg {
  width: 2rem;
  height: 2rem;
  color: #555;
}

@media screen and (max-width: 768px) {
  .about {
    padding: 1rem;
  }

  .about h2 {
    font-size: 1.5rem;
  }

  .prompt p {
    font-size: 1rem;
    margin-bottom: 1rem;
  }

  .icons-all a {
    width: 3rem;
    height: 3rem;
  }

  .icons-all a svg {
    width: 1.5rem;
    height: 1.5rem;
  }
}

@media screen and (max-width: 480px) {
  .about {
    padding: 0.5rem;
  }

  .about h2 {
    font-size: 1.2rem;
  }

  .prompt p {
    font-size: 0.8rem;
    margin-bottom: 0.5rem;
  }

  .icons-all a {
    width: 2.5rem;
    height: 2.5rem;
  }

  .icons-all a svg {
    width: 1.2rem;
    height: 1.2rem;
  }
}

/* Additional styles to make the profile photo responsive */

.about img {
