.experience {
    padding: 50px;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    justify-content: center;
  }
  
  .vertical-timeline {
    position: relative;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .vertical-timeline:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 100%;
    width: 2px;
    background-color: #3e497a;
  }
  
  .vertical-timeline-element {
    position: relative;
    margin: 50px 0;
    padding: 40px 20px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.3s ease;
    cursor: pointer;
  }
  
  .vertical-timeline-element:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }
  
  .vertical-timeline-element:before {
    content: '';
    position: absolute;
    top: 50%;
    left: -7px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    height: 14px;
    width: 14px;
    background-color: #3e497a;
    border-radius: 50%;
    transition: all 0.3s ease;
  }
  
  .vertical-timeline-element:hover:before {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  
  .vertical-timeline-element:after {
    content: '';
    position: absolute;
    top: 50%;
    left: -2px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background-color: #fff;
    border: 2px solid #3e497a;
    transition: all 0.3s ease;
  }
  
  .vertical-timeline-element:hover:after {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    border-color: #fff;
    background-color: #3e497a;
  }
  
  .vertical-timeline-element-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #3e497a;
    color: #fff;
    font-size: 20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 20px;
    transition: all 0.3s ease;
  }
  
  .vertical-timeline-element:hover .vertical-timeline-element-icon {
    background: #fff;
    color: #3e497a;
    border: 2px solid #3e497a;
  }
  
  .vertical-timeline-element-content {
    flex: 1 1;
    transition: all 0.3s ease;
  }
  
  .vertical-timeline-element-title {
    font-weight: bold;
    font-size: 22px;
    margin-bottom: 5px;
    transition: all 0.3s ease;
  }
  
  .vertical-timeline-element:hover .vertical-timeline-element-title {
    color: #3e497a;
  }
  
  .vertical-timeline-element-subtitle {
    font-size: 16px;
    margin-bottom: 10px;
    transition: all 0.3s ease;
  }
  
  
